
.profile__job {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  margin: 8px 0 0;
  max-width: 294px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  }

@media screen and (max-width:668px) {
  .profile__job {
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    margin: 0;
  }
}
