
.profile__info {
  display: grid;
  grid-template-columns: 1fr 24px;
  column-gap: 18px;
  align-self: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 668px) {
  .profile__info {
    padding: 26px 0 0 34px;
    gap: 9px 10px;
  }
}
